            "Baza sportiv\u0103 1 poate fi g\u0103sit\u0103 \u00EEn imediata vecin\u0103tate a facult\u0103\u021Bilor de Mecanic\u0103, Construc\u021Bii\n       sau a grupului Electro \u0219i a noii ...",
